import Cookie from 'js-cookie'
export default {
    state:{
        isCollapse: false,
        tabList:[
            {
                path:'/myHome',
                name:'home',
                label:'首页',
                icon:'s-home',
                url:'Home/Home'
            }
        ],
        menuData:[]
    },
    actions:{
        addTab(context,value){
            // 如果点击的页面路由名称不是首页home并且面包屑tabList中没有该路由则进行添加操作
            if(value.name!=='home'&&context.state.tabList.findIndex(item=>item.name===value.name)===-1){
                context.commit('addTab',value)
            }
        },
        removeTag(context,obj){
            const {tags,tag,vc,nowRouteName}=obj
            const nowIndex=tags.findIndex(item=>item.name===tag.name)
            context.state.tabList.splice(nowIndex,1)
            // 说明删除的不是当前路由 不用跳转
            if(tag.name!==nowRouteName){
                return console.log("删除的不是当前页面")
            }
            // 说明删除的是最后一个路由
            if(nowIndex===tags.length){
                // console.log("删除的是最后一个页面")
                vc.$router.push({name: tags[nowIndex - 1].name}).then(r=>r).catch()
            }else{
                // 删除的不是最后一个路由，直接传到最后就可以了
                vc.$router.push({name: tags[tags.length-1].name}).then(r=>r).catch()
            }
        },
    },
    mutations:{
        CollapseMenu(state){
            state.isCollapse=!state.isCollapse
        },
        addTab(state,value){
            state.tabList.push(value)
        },
        // 获取侧导航菜单数据
        getMenuData(state,data){
            state.menuData=data
            Cookie.set('menuData',JSON.stringify(data))
        },
        // 动态添加路由
        addRoutes(state,router){
            //判断路由中是否有数据
            if(!Cookie.get('menuData')) return
            const menu = JSON.parse(Cookie.get('menuData'))
            state.menuData=menu
            const menuArray=[]
            state.menuData.forEach(item => {
                if(item.children){
                    // 重新组装children
                    item.children= item.children.map(item =>{
                        item.component=()=>import(`../views/${item.url}`)
                        return item
                    })
                    menuArray.push(...item.children)
                }else{
                    item.component=()=>import(`../views/${item.url}`)
                    menuArray.push(item)
                }
            })
            console.log(menuArray)
            menuArray.forEach(item => {
                router.addRoute('Main',item)
            })
        }
    }
}