import Vue from 'vue'
import VueRouter from "vue-router";
import myMain from '../views/myMain'//内容展示页
import myLogin from '../views/myLogin'//登录页
Vue.use(VueRouter)

const router= new VueRouter({
    routes:[
        {
            path:'/',
            component:myMain,
            name:'Main',
            redirect:'/myHome',
            children:[]
        },
        {
            path:'/myLogin',
            name:'login',
            component:myLogin
        }
    ]
})
export default router