<template>
  <div class="header-container">
    <div class="l-content">
      <el-button icon="el-icon-menu" size="mini" @click="isCollapse"></el-button>
      <!--面包屑-->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item
            :to="{ path: '/' }"
            v-for="item in tabList"
            :key="item.name"
        >{{item.label}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown @command="handleClick">
        <span class="el-dropdown-link" >
            <img class="user" src="../assets/logo.png" alt="" >
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item >个人中心</el-dropdown-item>
          <el-dropdown-item command="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
import {mapState} from 'vuex'
export default {
  name: "contentHeader",
  methods:{
    isCollapse(){
      this.$store.commit('CollapseMenu')
    },
    handleClick(command){
      if(command==='logout'){
        Cookie.remove('token')
        Cookie.remove('menuData')
        this.$router.push({name:'login'})
      }
    }
  },
  computed:{
    ...mapState({
      tabList:state=>state.tab.tabList
    })
  }
}
</script>

<style lang="less" scoped>
.header-container{
  padding:0 20px;
  background-color:#333;
  height:60px;
  display:flex;
  justify-content: space-between;
  align-items:center;
  .l-content{
    display: flex;
    align-items: center;
    .el-breadcrumb{
      margin-left: 15px;
      /deep/.el-breadcrumb__item{
        .el-breadcrumb__inner{
          font-weight: normal;
          &.is-link{
            color: #666;
          }
        }
        &:last-child{
          .el-breadcrumb__inner{
            color: white;
          }
        }
      }
    }
  }
  .r-content{
    .el-dropdown{
        .user{
          display:block;
          height:40px;
          width:40px;
          border-radius:50%;
        }
    }
  }
}
.el-dropdown-link {
  cursor: pointer;
  color: #409EFF;
}

</style>