<template>
  <div class="tabs">
    <el-tag
        v-for="tag in tags"
        :key="tag.name"
        :closable="tag.name!=='home'"
        @click="togglePage(tag)"
        @close="removeTag(tag)"
        :type="tag.type"
        :effect="$route.name === tag.name ? 'dark' : 'plain'">
      {{tag.label}}
    </el-tag>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
  name: "contentTag",
  computed:{
    ...mapState({
      tags:state=>state.tab.tabList
    })
  },
  methods:{
    togglePage(tag){
      console.log(tag)
      this.$router.push({name:tag.name})
          .then(() => {}).catch(() => {
      });
    },
    removeTag(tag){
      this.$store.dispatch('removeTag',{
        nowRouteName:this.$route.name,
        tags:this.tags,
        vc:this,
        tag
      })
    }
  },
  mounted() {
  }
}
</script>

<style lang="less" scoped>
.el-tag{
  cursor: pointer;
}
</style>