<template>
  <el-menu
      :collapse="isCollapse"
      class="el-menu-vertical-demo"
      default-active="1-4-1"
      @close="handleClose"
      @open="handleOpen"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
    <h3>{{isCollapse ? '后台' : '通用后台管理系统'}}</h3>
    <el-menu-item @click="clickMenu(item)" v-for="item in noChildren" :key="item.name" :index="item.name">
      <i :class="`el-icon-${item.icon}`"></i>
      <span slot="title">{{item.label}}</span>
    </el-menu-item>
    <el-submenu v-for="item in hasChildren" :key="item.label" :index="item.label">
      <template slot="title">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </template>
      <el-menu-item-group>
        <el-menu-item v-for="child in item.children" :key="child.label" @click="clickMenu(child)" :index="child.name" >{{child.label}}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>

<script>
import Cookie from 'js-cookie'
export default {
  data() {
    return {};
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    clickMenu(item){
      console.log(item)
      this.$store.dispatch('addTab',item)
      this.$router.push({name:item.name})
          .then(() => {}).catch(() => {
      });
    }
  },
  computed:{
    noChildren(){
      return this.menuData.filter(item => !item.children)
    },
    hasChildren(){
      return this.menuData.filter(item => item.children)
    },
    menuData(){
      //判断当前数据，如果缓存中没有，去store中获取
      return JSON.parse(Cookie.get('menuData')) ||this.$store.state.menuData
    },
    isCollapse(){
      return this.$store.state.tab.isCollapse
    }
  },
  mounted() {

  }
}
</script>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 900px;
}
.el-menu{
  border:0;
  height:100%;
  h3{
    line-height: 48px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
  }
}
</style>