<template>
  <div class="main">
    <el-form ref="userForm"  :rules="rules" :model="userForm" label-width="80px">
      <h3>登录页面</h3>
      <el-form-item label="账号" prop="username">
        <el-input v-model="userForm.username" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="userForm.password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleLogin">登陆</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import Mock from 'mockjs'
import Cookie from 'js-cookie'
import {getMenu} from "@/api";
export default {
  name: "myLogin",
  data(){
    return {
      userForm:{
        username:'',
        password:''
      },
      rules:{
        username:[{required: true, message: '请输入账号', trigger: 'blur'}],
        password:[{required: true, message: '请输入密码', trigger: 'blur'}]
      }
    }
  },
  methods:{
    handleLogin(){
      // //token信息存入cookie用于不同页面间通信
      getMenu(this.userForm).then(({ data })=>{
        if(data.code===20000){
          Cookie.set('token',data.data)
          //获取menu添加到vuex中
          this.$store.commit('getMenuData',data.data.menu)
          this.$store.commit('addRoutes',this.$router)
          this.$router.push({name:'home'})
        }else{
          this.$message.error(data.data.message);
        }
      })
    }
  },
  mounted() {

  }
}
</script>

<style lang="less" scoped>
.main{
  background-image: url("../assets/images/szlSKE65ir.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .el-form{
    width: 350px;
    padding: 20px;
    background-color: white;
    /deep/.el-input{
      &.el-input_inner{
      }
    }
  }
}
</style>